const items = {};
export function isTestMode() {
    return typeof localStorage === 'undefined';
}
export function createTestStorage() {
    return {
        getItem: (name) => {
            return items[name] ? JSON.parse(items[name]) : null;
        },
        setItem: (name, value) => {
            items[name] = JSON.stringify(value);
        },
        removeItem: (name) => {
            delete items[name];
        }
    };
}
