import * as React from 'react';
import { useCallback, useEffect, useRef } from 'react';
import { dataFrameCheckerPython, parsePythonResult } from '../utils/dataframe-checker';
import { NotebookActions } from '@jupyterlab/notebook';
import { KernelMessage } from '@jupyterlab/services';
import { AssistantPanel } from './assistant-panel';
import { extractErrorContext } from './notebook-error-helper';
import { useInterval } from 'usehooks-ts';
import { convertSelection, getSelectionFromEditor, isSelectionEqual } from '../utils/text-selection-utils';
import { AssistantLogin } from './assistant-login';
import { postExecutionResult, useStore } from '@anaconda/assistant-components';
import { isLocalNotebooks } from '../utils/environmentType';
import { syncDiskStateChained } from '../jupyter/syncStateDebounced';
import { getNewChatStarters } from '../utils/ChatStarters';
import { getAssistantSdk } from '../assistant-sdk';
export function AssistantPanelContainer({ tracker }) {
    const [account, path, setPath, setCellError] = useStore(state => [
        state.account,
        state.path,
        state.setPath,
        state.setCellError
    ]);
    const restarted = useRef(false);
    const executingCode = useRef(null);
    const jupyterSelection = useRef(null);
    useInterval(() => {
        // Every 800ms we check the current text selection
        checkTextSelection(tracker);
    }, 800);
    const checkNotebookVariables = useCallback(() => {
        const requestExecute = getKernel(tracker)?.requestExecute({ code: dataFrameCheckerPython }, false);
        if (requestExecute) {
            requestExecute.onIOPub = (msg) => {
                if (KernelMessage.isExecuteResultMsg(msg)) {
                    if (msg.content.data && msg.content.data['text/plain']) {
                        const nbState = parsePythonResult(msg.content.data['text/plain']);
                        if (nbState) {
                            const path = tracker.currentWidget?.context.path || '';
                            setPath(path, nbState, getNewChatStarters);
                        }
                    }
                }
            };
        }
    }, [setPath, tracker]);
    const cellExecuted = useCallback((_, context) => {
        const { cell, notebook, success, error } = context;
        if (!success && error) {
            setCellError(extractErrorContext(cell, notebook, error.message));
        }
        if (executingCode.current?.cellId && executingCode.current?.cellId === cell.model.id) {
            postExecutionResult(getAssistantSdk(), { ...executingCode.current }, success, error);
            executingCode.current = null;
        }
        checkNotebookVariables();
    }, [setCellError]);
    useEffect(() => {
        function notebookChanged(_, context) {
            // We want to set the path to the new notebook as soon as we switch, even if we don't
            // have the notebook state yet.
            setPath(context?.sessionContext.path || '', { user: '', dataframes: [] }, getNewChatStarters);
            // When the notebook changes, we sync the disk state to the store
            syncDiskStateChained();
            if (!context?.sessionContext.session) {
                // When opening a new notebook, the session may not be available yet, so we need to
                // wait a bit before checking the notebook variables.
                setTimeout(checkNotebookVariables, 1000);
            }
            else {
                checkNotebookVariables();
            }
        }
        function kernelStatusChanged(_, status) {
            if (status === 'restarting' || status === 'autorestarting') {
                restarted.current = true;
            }
            else if (status === 'idle' && restarted.current) {
                restarted.current = false;
                checkNotebookVariables();
            }
        }
        checkNotebookVariables();
        NotebookActions.executed.connect(cellExecuted);
        tracker.currentChanged.connect(notebookChanged);
        getKernel(tracker)?.statusChanged.connect(kernelStatusChanged);
        return () => {
            NotebookActions.executed.disconnect(cellExecuted);
            tracker.currentChanged.disconnect(notebookChanged);
            getKernel(tracker)?.statusChanged.disconnect(kernelStatusChanged);
        };
    }, [checkNotebookVariables, cellExecuted, setPath, tracker]);
    function checkTextSelection(tracker) {
        const editor = tracker.currentWidget?.content?.activeCell?.editor;
        const isMarkdown = editor?.model.mimeType === 'text/x-ipythongfm';
        if (editor && !isMarkdown) {
            const selection = editor.getSelection();
            // We use the jupyterSelection ref as a first check to see if any selection has
            // changed. If it has, we update the selected text on the global store
            const selectionEqual = isSelectionEqual(jupyterSelection.current, selection);
            if (!selectionEqual) {
                const selectedText = getSelectionFromEditor(editor, selection);
                useStore.getState().setTextSelection(selectedText);
                jupyterSelection.current = convertSelection(selection);
            }
        }
    }
    async function addCodeToNotebook(code, activeMessageId, session, inPlace = false) {
        const notebook = tracker.currentWidget;
        const _addCodeToNotebook = useStore.getState().settings.addCodeToNotebook || 'below-active-cell';
        if (notebook) {
            if (!inPlace) {
                switch (_addCodeToNotebook) {
                    case 'below-active-cell':
                        NotebookActions.insertBelow(notebook.content);
                        break;
                    case 'at-end-of-notebook':
                        notebook.content.activeCellIndex = notebook.content.widgets.length - 1;
                        NotebookActions.insertBelow(notebook.content);
                        break;
                    case 'in-place':
                        break;
                }
            }
            await notebook.content?.activeCell?.ready;
            NotebookActions.replaceSelection(notebook.content, code);
            const cellId = notebook.content.activeCell?.model.id;
            executingCode.current = { code, cellId, activeMessageId, session };
            await NotebookActions.run(notebook.content, notebook.sessionContext);
        }
    }
    function getActiveCodeCell() {
        // TODO: This seems only needed in v3 in the monorepo, not in the jupyterassistant repo
        return (tracker.currentWidget?.content.activeCell?.model.sharedModel).source || '';
    }
    if (!account && isLocalNotebooks()) {
        return React.createElement(AssistantLogin, null);
    }
    return path ? (React.createElement(AssistantPanel, { tracker: tracker, addCodeToNotebook: addCodeToNotebook, getActiveCodeCell: getActiveCodeCell })) : null;
}
function getKernel(tracker) {
    return tracker.currentWidget?.sessionContext.session?.kernel;
}
