import { AssistantAPIInvalidResponseError, AssistantAPIServerError } from '../publicTypes';
/**
 * Reads the last available version of the terms and conditions,
 * which should be used to force the user to accept the new ones if they have changed.
 * @internal
 */
export async function fetchTermsAndConditionsVersion(opts) {
    const { http } = opts;
    const { request, response } = await http.fetchSafely({
        path: '/terms-and-conditions-version',
        method: 'GET'
    });
    if (response.status !== 200) {
        throw new AssistantAPIServerError('Unable to fetch terms and conditions version.', {
            request,
            response
        });
    }
    try {
        const data = await response.json();
        return data.version;
    }
    catch (e) {
        throw new AssistantAPIInvalidResponseError('Unable to parse terms and conditions version.', {
            request,
            response
        });
    }
}
