import { ANACONDA_ASSISTANT_SUPPORTED_API_URLS, AssistantSDKInvalidAPIURLError } from './publicTypes';
export function isAE5APIUrl(url) {
    // API URLs that match "https://*.dsp.anaconda.com" are considered AE5 API URLs
    return url.startsWith('https://') && url.endsWith('.dsp.anaconda.com');
}
export function validateAPIUrl(url) {
    const _url = url;
    if (!_url || (ANACONDA_ASSISTANT_SUPPORTED_API_URLS.indexOf(_url) === -1 && !isAE5APIUrl(_url))) {
        throw new AssistantSDKInvalidAPIURLError(`Invalid API URL: ${url}`);
    }
}
