export function createDataframe(name, hash) {
    return {
        name,
        hash: hash || 'dataframe_hash',
        type: 'DataFrame',
        cols: [
            ['PassengerId', 'int64'],
            ['Survived', 'int64']
        ],
        dtypesStr: 'dtypes_str'
    };
}
