import { ASSISTANT_API_SERVER_ERROR_NAME, AssistantAPINetworkError, AssistantAPIServerError } from './publicTypes';
/**
 * Anaconda Assistant HTTP Client
 * @alpha
 */
export class AnacondaAssistantHTTPClient {
    _apiUrl;
    _getHeaders;
    constructor(opts) {
        this._apiUrl = opts.apiUrl;
        this._getHeaders = opts.getHeaders;
    }
    async fetchSafely(options) {
        const { path, body, method, abortController } = options;
        const headers = this._getHeaders();
        const request = new Request(`${this._apiUrl}${path}`, {
            method,
            headers,
            body,
            signal: abortController?.signal
        });
        try {
            const response = await fetch(request);
            if (response.status > 299) {
                const text = await response.text();
                if (matchesKnownError(text)) {
                    throw new AssistantAPIServerError(text, { request, response });
                }
                throw new AssistantAPIServerError('There was an issue connecting to the Assistant server. Please try again.', { request, response });
            }
            return { request, response };
        }
        catch (e) {
            if (e.name === ASSISTANT_API_SERVER_ERROR_NAME) {
                throw e;
            }
            throw new AssistantAPINetworkError('There was an issue connecting to the Assistant server. Please try again.', { request });
        }
    }
}
export function matchesKnownError(text) {
    if (text.includes('Too many requests') ||
        text.includes('requests allowed exceeded') ||
        text.includes('maximum context length') ||
        text.includes('org_blacklisted')) {
        return true;
    }
    return false;
}
