import { URLExt } from '@jupyterlab/coreutils';
import { ServerConnection } from '@jupyterlab/services';
import { ASSISTANT_EXTENSION_NAMESPACE } from '../config';
/**
 * Call the API extension
 *
 * @param endPoint API REST end point for the extension
 * @param init Initial values for the request
 * @returns The response body interpreted as JSON
 */
export async function extensionAPIRequest(endPoint = '', init = {}) {
    // Make request to Jupyter API
    const settings = ServerConnection.makeSettings();
    const requestUrl = URLExt.join(settings.baseUrl, ASSISTANT_EXTENSION_NAMESPACE, // API Namespace
    endPoint);
    let response;
    try {
        response = await ServerConnection.makeRequest(requestUrl, init, settings);
    }
    catch (error) {
        throw new ServerConnection.NetworkError(error);
    }
    let data = await response.text();
    if (data.length > 0) {
        try {
            data = JSON.parse(data);
        }
        catch (error) {
            console.error(error);
        }
    }
    if (!response.ok) {
        throw new ServerConnection.ResponseError(response, data.message || data);
    }
    return data;
}
export async function getJson(response) {
    if (!response.ok) {
        try {
            const text = await response.text();
            return Promise.reject(`Failed with status ${response.status} and body ${text}`);
        }
        catch (e) {
            return Promise.reject(`Failed with status ${response.status}`);
        }
    }
    return response.json();
}
export async function getJsonFromExtension(response) {
    if (response.remote_status_code < 200 || response.remote_status_code >= 300) {
        throw new Error(`Failed with status ${response.remote_status_code} and body ${response.remote_data}`);
    }
    return response.remote_data;
}
