const exportMessageToNotebook = (tracker) => async (message) => {
    const notebook = tracker.currentWidget;
    if (!notebook)
        return;
    let addCodeToNotebook = useStore.getState().settings.addCodeToNotebook || 'below-active-cell';
    if (addCodeToNotebook === 'in-place') {
        addCodeToNotebook = 'below-active-cell';
    }
    const messageBlocks = extractMessageBlocks(message.content);
    let hasPrintedRole = false;
    for (const block of messageBlocks) {
        switch (addCodeToNotebook) {
            case 'below-active-cell':
                NotebookActions.insertBelow(notebook.content);
                break;
            case 'at-end-of-notebook':
                notebook.content.activeCellIndex = notebook.content.widgets.length - 1;
                NotebookActions.insertBelow(notebook.content);
                break;
        }
        if (block.type === 'python') {
            NotebookActions.changeCellType(notebook.content, 'code');
        }
        else {
            NotebookActions.changeCellType(notebook.content, 'markdown');
        }
        const role = message.role === 'user' ? 'User' : 'Assistant';
        const parts = [`# ${role}`, block.content];
        if (hasPrintedRole) {
            parts.shift();
        }
        else {
            hasPrintedRole = true;
        }
        const content = parts.join('\n');
        await notebook.content?.activeCell?.ready;
        NotebookActions.replaceSelection(notebook.content, content);
        await NotebookActions.run(notebook.content, notebook.sessionContext);
    }
};
