import { getJson, extensionAPIRequest } from '../request';
import { getCookies } from '../getCookies';
import { isEnterpriseNotebooks, isLocalDev, isLocalNotebooks } from '../environmentType';
import { ENV } from '../../config';
let statePromise = null;
const authenticationMethod = [
    {
        unavailable: false,
        getUrl: () => `${ENV.PROD.LEGACY_CLOUD_API_BASE_URL}/iam/token`,
        getOptions: () => ({
            method: 'POST',
            credentials: 'include',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({ grant_type: 'refresh_token' })
        })
    },
    {
        unavailable: false,
        getUrl: () => `${ENV.PROD.CLOUD_API_BASE_URL}/iam/token`,
        getOptions: () => ({
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                grant_type: 'refresh_token',
                refresh_token: getCookies().refresh_token
            })
        })
    },
    {
        unavailable: false,
        getUrl: () => `${ENV.PROD.STAGING_CLOUD_API_BASE_URL}/iam/token`,
        getOptions: () => ({
            method: 'POST',
            headers: { 'Content-Type': 'application/json' },
            body: JSON.stringify({
                grant_type: 'refresh_token',
                refresh_token: getCookies().refresh_token
            })
        })
    }
];
async function initState() {
    let tokenResponse = null;
    for (const method of authenticationMethod) {
        if (method.unavailable) {
            continue;
        }
        console.log('[Assistant] Trying to get access token from', method.getUrl());
        try {
            tokenResponse = await fetch(method.getUrl(), method.getOptions());
            if (tokenResponse.status === 200) {
                break;
            }
        }
        catch (e) {
            console.error(e);
            method.unavailable = true;
        }
    }
    if (!tokenResponse || tokenResponse.status !== 200) {
        throw new Error('Unable to get access token');
    }
    const { access_token, expires_in } = await getJson(tokenResponse);
    const expires = new Date();
    expires.setSeconds(expires.getSeconds() + expires_in);
    return { token: access_token, expires };
}
let tokenPromise = null;
export function getNucleusAccessToken(path) {
    if (tokenPromise) {
        return tokenPromise;
    }
    tokenPromise = (async () => {
        const response = await extensionAPIRequest(path);
        if (response.access_token === null) {
            return null;
        }
        return {
            accessToken: response.access_token,
            expiresAt: response.expires_at
        };
    })();
    return tokenPromise;
}
export function clearTokenPromise() {
    tokenPromise = null;
}
export async function getAccessToken(loopCount = 0) {
    // If localhost, don't try to get an access token
    if (isLocalDev()) {
        return null;
    }
    // If local notebooks
    if (isLocalNotebooks()) {
        return getNucleusAccessToken('nucleus_token');
    }
    // If enterprise notebooks
    if (isEnterpriseNotebooks()) {
        return getNucleusAccessToken('ae5_nucleus_token');
    }
    // Otherwise, get the access token
    if (loopCount > 2) {
        throw new Error('The access token expires_at is causing a loop');
    }
    if (!statePromise) {
        statePromise = initState();
    }
    const state = await statePromise;
    const now = new Date();
    // Invalidate the token when it gets within 30 seconds of expiration
    // to avoid clock drift etc.
    if (now >= state.expires) {
        statePromise = null;
        return getAccessToken(loopCount++);
    }
    return {
        accessToken: state.token,
        expiresAt: state.expires.getTime()
    };
}
