import { AnacondaAssistant } from '@anaconda/assistant-sdk';
import { getAccessToken } from './utils/nucleus/auth';
let assistantSdk;
export function getAssistantSdk() {
    if (!assistantSdk) {
        throw new Error('Assistant not initialized');
    }
    return assistantSdk;
}
export async function initAssistantSdk(options) {
    assistantSdk = new AnacondaAssistant(options);
    try {
        const accessToken = await getAccessToken();
        if (!accessToken) {
            throw new Error('No access token');
        }
        assistantSdk.accessToken.setAccessToken(accessToken);
    }
    catch (e) {
        console.log('Error fetching access: ', e);
    }
    return assistantSdk;
}
