import { isEnterpriseNotebooks } from '../environmentType';
import { getJsonFromExtension, extensionAPIRequest } from '../request';
import { getIsProTier, getIsStarterTier } from './accountUtils';
import { getAccessToken } from './auth';
/**
 * Returns the Nucleus account of the current user.
 * This function is memoized, so it will only make one request to the server
 * until the page is reloaded.
 */
let nucleusAccountPromise = null;
export function getNucleusAccount() {
    if (isEnterpriseNotebooks()) {
        return Promise.resolve({
            metadata: {
                is_starter_tier: false,
                is_pro_tier: true,
                is_enterprise_tier: true
            },
            profile: {
                is_confirmed: true,
                is_disabled: false
            },
            subscriptions: [],
            user: {
                id: '0',
                company: 'Anaconda Enterprise',
                company_size: 'none',
                country: null,
                created_at: '0',
                email: 'Anaconda Enterprise',
                external_id: undefined,
                first_home_visit: false,
                first_name: undefined,
                last_name: undefined,
                industry: undefined,
                partner_customer_id: undefined,
                partner_id: undefined,
                position: undefined,
                promotional_content: undefined,
                state: 'TX',
                updated_at: '0'
            }
        });
    }
    if (nucleusAccountPromise) {
        return nucleusAccountPromise;
    }
    nucleusAccountPromise = (async () => {
        const accessToken = await getAccessToken();
        if (!accessToken) {
            throw new Error('No access token');
        }
        const accountResponse = await extensionAPIRequest('nucleus_user', {
            headers: { 'X-Nucleus-Token': accessToken.accessToken }
        });
        const account = await getJsonFromExtension(accountResponse);
        const isStarterTier = getIsStarterTier(account);
        const isProTier = getIsProTier(account);
        return {
            ...account,
            metadata: {
                is_starter_tier: isStarterTier,
                is_pro_tier: isProTier
            }
        };
    })();
    return nucleusAccountPromise;
}
/**
 * Clears the memoized Nucleus account.
 */
export function clearNucleusAccountPromise() {
    nucleusAccountPromise = null;
}
