import { AssistantSupportedEndpointVersions, AssistantAPIServerError, AssistantAPIInvalidResponseError, validateVersion } from '../publicTypes';
/**
 * Fetch suggestions
 * @internal
 */
export async function fetchSuggestions(opts) {
    const { version, http, request, abortController } = opts;
    validateVersion(version, AssistantSupportedEndpointVersions.FETCH_SUGGESTIONS_SUPPORTED_VERSIONS);
    const { request: fetchRequest, response } = await http.fetchSafely({
        method: 'POST',
        path: `/${version}/suggestions`,
        body: JSON.stringify(request),
        abortController
    });
    if (response.status !== 200) {
        throw new AssistantAPIServerError('Unable to fetch suggestions.', {
            request: fetchRequest,
            response
        });
    }
    let suggestions;
    try {
        suggestions = (await response.json()).suggestions;
    }
    catch (e) {
        throw new AssistantAPIInvalidResponseError('Unable to parse suggestions response.', {
            request: fetchRequest,
            response
        });
    }
    if (!Array.isArray(suggestions)) {
        throw new AssistantAPIInvalidResponseError('Received suggestions is not an array.', {
            request: fetchRequest,
            response
        });
    }
    return suggestions;
}
