import { STATE_VERSION, useStore } from '@anaconda/assistant-components';
import { extensionAPIRequest } from '../utils/request';
import { getAccessToken } from '../utils/nucleus/auth';
/**
 * Synchronizes the state of the store with the server.
 * Does so by sending the state to the server,
 * except for the terms and conditions, which
 * are only sent if they have been accepted.
 */
export async function syncDiskState(termsAccepted) {
    const accessToken = await getAccessToken();
    if (!accessToken) {
        console.log('Could not sync state, no access token');
        return;
    }
    const appState = useStore.getState();
    const state = {
        ...appState,
        terms: {
            accepted: false,
            version: appState.terms?.version || null
        }
    };
    // Special case for terms and conditions
    if (appState.terms?.accepted === true || termsAccepted) {
        state.terms.accepted = true;
    }
    extensionAPIRequest('sync_disk_state', {
        headers: { 'X-Nucleus-Token': accessToken.accessToken },
        method: 'POST',
        body: JSON.stringify({
            state,
            version: STATE_VERSION
        })
    });
}
export async function clearDiskState() {
    const accessToken = await getAccessToken();
    if (!accessToken) {
        console.log('Could not sync state, no access token');
        return;
    }
    extensionAPIRequest('sync_disk_state', {
        headers: { 'X-Nucleus-Token': accessToken.accessToken },
        method: 'POST',
        body: JSON.stringify({})
    });
}
