import { AssistantAPIServerError, AssistantSupportedEndpointVersions, validateVersion } from '../publicTypes';
/**
 * Post feedback
 * @internal
 */
export async function proFetchSummary(opts) {
    const { version, http, request } = opts;
    validateVersion(version, AssistantSupportedEndpointVersions.PRO_SUMMARIES_SUPPORTED_VERSIONS);
    const { request: fetchRequest, response } = await http.fetchSafely({
        method: 'POST',
        path: `/${version}/pro/summaries`,
        body: JSON.stringify(request)
    });
    if (response.status !== 200) {
        throw new AssistantAPIServerError('Unable to create summary', {
            request: fetchRequest,
            response
        });
    }
    const json = await response.json();
    return {
        summary: json.data.summary,
        summaryDate: json.data.summary_date,
        summaryTechnique: json.data.summary_technique
    };
}
