import { AssistantError } from '@anaconda/assistant-components';
import { getAssistantSdk } from '../assistant-sdk';
export async function fetchTermsAndConditionsVersion() {
    try {
        return await getAssistantSdk().fetchTermsAndConditionsVersion();
    }
    catch (e) {
        throw new AssistantError('network_error', 'There was an issue connecting to the Assistant server. Please try again.', 0);
    }
}
