import { useStore } from '@anaconda/assistant-components';
import { getSelectedTextAndUpdateStore } from './getSelectedTextAndUpdateStore';
export function getActivateContextMenuAssistantWithCodeSelection(opts) {
    return async function () {
        const { assistant, command, notebookTracker } = opts;
        const selectedText = getSelectedTextAndUpdateStore(notebookTracker);
        assistant.activateWidget(false);
        useStore.getState().resetToHomeScreen();
        useStore.getState().makeChatRequest({
            context: {
                type: command,
                variables: {
                    active_cell_code: selectedText?.cell.source ?? '',
                    selected_code: (selectedText?.text || selectedText?.cell.source) ?? ''
                }
            },
            instruction: ''
        });
    };
}
