import { getAccessToken } from '../utils/nucleus/auth';
import { getAssistantSdk } from '../assistant-sdk';
export async function hasAccess() {
    try {
        const accessToken = await getAccessToken();
        if (!accessToken) {
            throw new Error('No access token');
        }
        const sdk = getAssistantSdk();
        sdk.accessToken.setAccessToken(accessToken);
        await sdk.fetchAccess();
        return true;
    }
    catch (e) {
        console.log('Error fetching access: ', e);
    }
    return false;
}
