import { AssistantAPIServerError, AssistantSupportedEndpointVersions, validateVersion } from '../publicTypes';
/**
 * Post feedback
 * @internal
 */
export async function submitFeedback(opts) {
    const { version, http, request } = opts;
    validateVersion(version, AssistantSupportedEndpointVersions.SUBMIT_FEEDBACK_SUPPORTED_VERSIONS);
    const { request: fetchRequest, response } = await http.fetchSafely({
        method: 'POST',
        path: `/${version}/feedback`,
        body: JSON.stringify(request)
    });
    if (response.status !== 201) {
        throw new AssistantAPIServerError('Unable to post feedback', {
            request: fetchRequest,
            response
        });
    }
    return true;
}
