import React, { useEffect } from 'react';
import { attachmentDataframeInstruction } from '../utils';
import { SelectDataFrame } from './SelectDataframe';
import { Popup } from '../base-elements';
/**
 * Add Context Popup component.
 * @alpha
 */
export const AddContextPopup = (props) => {
    const { width, onAddContext, dataFrames, activeChatAvailable, setAttachContextOpen, attachContextOpen, makeChatRequest, enableDataCollection } = props;
    useEffect(() => {
        if (!dataFrames?.length) {
            setAttachContextOpen(false);
        }
    }, [dataFrames, setAttachContextOpen]);
    function closeExplainDialog() {
        setAttachContextOpen(false);
    }
    return (React.createElement(Popup, { width: width, show: attachContextOpen, headerTitle: 'Anaconda Assistant', warningMessage: `By attaching a dataframe to the conversation, the dataframe column names and types will be
                  sent to ${enableDataCollection ? 'the Anaconda Servers and to ' : ''}
                  OpenAI.`, bodyTitle: 'Select Dataframe', body: React.createElement(SelectDataFrame, { makeChatRequest: makeChatRequest, dataFrames: dataFrames, buttonLabel: activeChatAvailable
                ? 'Attach dataframe to current chat'
                : 'Attach dataframe to new chat', onSubmit: dataframe => {
                setAttachContextOpen(false);
                const instruction = attachmentDataframeInstruction(dataframe.name, dataframe.cols);
                onAddContext(instruction);
            } }), dialogClosed: closeExplainDialog }));
};
