import { AssistantAPIInvalidResponseError, AssistantSupportedEndpointVersions, validateVersion } from '../publicTypes';
import { containsTokenCount, parseMaxTokenCount, parseTokenCount, removeTokenCount } from '../responseTokens';
/**
 * Stream response
 * @internal
 */
export async function* streamResponse(opts) {
    const { version, http, request, abortController } = opts;
    validateVersion(version, AssistantSupportedEndpointVersions.STREAM_RESPONSE_SUPPORTED_VERSIONS);
    // Similar streamed call as to OpenAI but to https://assistant.anaconda.cloud/v2/completions
    const { request: fetchRequest, response } = await http.fetchSafely({
        method: 'POST',
        path: `/${version}/completions`,
        body: JSON.stringify(request),
        abortController
    });
    if (!response.body) {
        throw new AssistantAPIInvalidResponseError('Empty stream when parsing response', {
            request: fetchRequest,
            response
        });
    }
    const reader = response.body.getReader();
    const decoder = new TextDecoder();
    let done = false;
    let chunk = '';
    while (!done) {
        const { value, done: doneReading } = await reader.read();
        done = doneReading;
        const chunkValue = decoder.decode(value);
        chunk = chunk + chunkValue;
        if (!containsTokenCount(chunk)) {
            yield {
                content: chunk
            };
        }
        else {
            const tokens = parseTokenCount(chunk);
            const maxTokens = parseMaxTokenCount(chunk);
            yield {
                content: removeTokenCount(chunk),
                tokens,
                maxTokens
            };
        }
    }
}
