import { AssistantAPIServerError, AssistantSupportedEndpointVersions, validateVersion } from '../publicTypes';
/**
 * Submit code executed, used to study effectiveness of generated code
 * @internal
 */
export async function submitCodeExecutionResult(opts) {
    const { version, http, request, abortController } = opts;
    validateVersion(version, AssistantSupportedEndpointVersions.SUBMIT_CODE_EXECUTION_SUPPORTED_VERSIONS);
    const { request: fetchRequest, response } = await http.fetchSafely({
        method: 'POST',
        path: `/v2/execution_result`,
        body: JSON.stringify(request),
        abortController
    });
    if (response.status !== 201) {
        throw new AssistantAPIServerError('Unable to fetch suggestions.', {
            request: fetchRequest,
            response
        });
    }
    return true;
}
