import { CodeCell } from '@jupyterlab/cells';
import { getSelectionFromEditor } from '../utils/text-selection-utils';
import { useStore } from '@anaconda/assistant-components';
export function getSelectedTextAndUpdateStore(tracker) {
    const activeCell = tracker.activeCell;
    const editor = activeCell?.editor;
    if (!editor) {
        return;
    }
    // If there is a selection, use it
    const currentSelection = editor?.getSelection();
    const selectedText = getSelectionFromEditor(editor, currentSelection);
    if (selectedText.text) {
        useStore.getState().setTextSelection(selectedText);
        return selectedText;
    }
    // Only select text in code cells
    if (activeCell instanceof CodeCell) {
        const lastLine = editor.getLine(editor.lineCount - 1) || '';
        const totalLines = editor.lineCount - 1;
        editor.setSelection({
            start: { line: 0, column: 0 },
            end: { line: totalLines, column: lastLine.length }
        });
        const selection = editor.getSelection();
        const selectedText = getSelectionFromEditor(editor, selection);
        useStore.getState().setTextSelection(selectedText);
    }
    return selectedText;
}
