import { sleep } from './testUtils';
import { AssistantError } from './errors';
export function graphChatType(df1) {
    return {
        type: 'graph-dataframe',
        variables: { dataframe: df1 }
    };
}
export const exampleCodeResponses = [
    'imp',
    'import',
    'import matplot',
    'import matplotlib.pyplot',
    'import matplotlib.pyplot as plt',
    'import matplotlib.pyplot as plt\ndf.plot()'
];
export async function* mockStream(request, options) {
    let aborted = false;
    options?.abortController?.signal.addEventListener('abort', () => (aborted = true));
    for (const item of exampleCodeResponses) {
        if (aborted) {
            throw new DOMException('Aborted', 'AbortError');
        }
        await sleep(100);
        yield {
            content: item,
            tokens: Math.floor(item.length / 4),
            maxTokens: 0
        };
    }
}
export async function* mockStreamWithError(request, options) {
    let count = 0;
    for (const item of exampleCodeResponses) {
        if (count++ === 2)
            throw new AssistantError('network error', 'network error', 400);
        await sleep(100);
        yield {
            content: item,
            tokens: Math.floor(item.length / 4),
            maxTokens: 0
        };
    }
}
