import { AssistantIcon } from './components/icon';
import { AssistantButton } from './components/assistant-button';
import { AssistantSidebarWidget } from './jupyter/assistantSidebarWidget';
import { getActivateAssistantWithCodeSelection } from './jupyter/activateAssistantWithCodeSelection';
import { getActivateContextMenuAssistantWithCodeSelection } from './jupyter/activateContextMenuAssistantWithCodeSelection';
import { initializeAssistant } from './jupyter/initializeAssistant';
import { initAssistantStore, useStore } from '@anaconda/assistant-components';
import { sharedCore } from './jupyter/extension-load-utils';
import { initAssistantSdk, getAssistantSdk } from './assistant-sdk';
import { CLIENT_VERSION } from './config';
import { bindSyncDiskStateChained, syncDiskStateChained, unbindSyncDiskStateChained } from './jupyter/syncStateDebounced';
import { logout, shareJupyterFrontEndWithLogin } from './utils/nucleus/accountUtils';
import { syncFilesForSummarization } from './utils/fileSummarizationUtils';
import { getServerUrl } from './config';
import { getNucleusAccount } from './utils/nucleus/account';
import { getAccountMaxTokens } from './utils/nucleus/accountUtils';
import { isLocalDev } from './utils/environmentType';
import { DELETED_CHAT_KEYS } from './jupyter/fetchDiskState';
// This command doesn't do anything, but needs to be defined for the menu item to show up
export const assistantMenuHeader = 'assistant:context-header';
export const assistantRefactorCommand = 'assistant:refactor';
export const assistantCommentCommand = 'assistant:comment';
export const assistantExplainCommand = 'assistant:explain';
export const assistantToggleCommand = 'assistant:toggle';
export const assistantFocusChatInputCommand = 'assistant:focusInput';
export const assistantCellCommand = 'assistant:cell';
export default class AssistantPlugin {
    async activate(app, opts) {
        const { corePlugin, notebookTracker, palette, isJupyterNotebook = false } = opts;
        await sharedCore.setCore(corePlugin);
        if (!sharedCore.getFeatureFlagBool('monorepo-assistant')) {
            return;
        }
        // Allowing login to create new notebooks
        shareJupyterFrontEndWithLogin(app);
        const config = sharedCore.getCoreReady().config;
        await initAssistantSdk({
            clientVersion: CLIENT_VERSION,
            // The API URL might come as null from the shared core
            apiUrl: config.assistant.apiUrl || undefined,
            environmentType: config.assistant.environmentType,
            enableDataCollection: false
        });
        await initAssistantStore({
            getAssistantSdk,
            getServerUrl,
            getNucleusAccount,
            getAccountMaxTokens,
            isLocalDev,
            syncDiskStateChained,
            unbindSyncDiskStateChained,
            deletedChatKeys: DELETED_CHAT_KEYS,
            getFeatureFlagBool: sharedCore.getCoreReady().featureFlag.getBool
        });
        if (useStore.getState().settings.enableDataCollection) {
            getAssistantSdk().enableDataCollection();
        }
        console.log('Assistant extension activated!');
        // Sync state across different ports / windows
        await syncDiskStateChained();
        bindSyncDiskStateChained();
        // Sync files for summarization
        syncFilesForSummarization();
        const assistantSidebarWidget = new AssistantSidebarWidget({
            notebookTracker,
            jupyterFrontEnd: app,
            isJupyterNotebook
        });
        const assistantButton = new AssistantButton(app.commands);
        initializeAssistant({ assistant: assistantSidebarWidget, jupyterFrontEnd: app });
        app.commands.addCommand(assistantMenuHeader, {
            icon: AssistantIcon,
            label: 'AI Assistant',
            isEnabled: () => false,
            // This menu item is just a heading. It doesn't need to do anything but this function is required.
            execute: () => { }
        });
        app.commands.addCommand(assistantRefactorCommand, {
            label: 'Refactor Code',
            execute: getActivateContextMenuAssistantWithCodeSelection({
                assistant: assistantSidebarWidget,
                command: 'improve-code',
                notebookTracker
            })
        });
        app.commands.addCommand(assistantCommentCommand, {
            label: 'Comment Code',
            execute: getActivateContextMenuAssistantWithCodeSelection({
                assistant: assistantSidebarWidget,
                command: 'comment-code',
                notebookTracker
            })
        });
        app.commands.addCommand(assistantExplainCommand, {
            label: 'Explain Code',
            execute: getActivateContextMenuAssistantWithCodeSelection({
                assistant: assistantSidebarWidget,
                command: 'explain-code',
                notebookTracker
            })
        });
        app.commands.addCommand(assistantToggleCommand, {
            label: 'Open Anaconda Assistant',
            execute: () => assistantSidebarWidget.toggleWidget()
        });
        app.commands.addCommand(assistantFocusChatInputCommand, {
            label: 'Focus Anaconda Assistant Chat Input Field',
            execute: () => {
                const assistantChatInputTextarea = document.querySelector('#as-instruction-input-textarea');
                if (assistantChatInputTextarea instanceof HTMLElement) {
                    assistantChatInputTextarea.focus();
                }
            }
        });
        app.contextMenu.addItem({
            rank: 0,
            command: assistantMenuHeader,
            selector: '.jp-Notebook'
        });
        app.contextMenu.addItem({
            rank: 0,
            command: assistantRefactorCommand,
            selector: '.jp-Notebook'
        });
        app.contextMenu.addItem({
            rank: 0,
            command: assistantCommentCommand,
            selector: '.jp-Notebook'
        });
        app.contextMenu.addItem({
            rank: 0,
            command: assistantExplainCommand,
            selector: '.jp-Notebook'
        });
        app.contextMenu.addItem({
            rank: 0,
            command: assistantToggleCommand,
            selector: '.jp-Notebook'
        });
        app.contextMenu.addItem({
            rank: 0,
            selector: '.jp-Notebook',
            type: 'separator'
        });
        palette.addItem({ command: assistantToggleCommand, category: 'Assistant' });
        app.docRegistry.addWidgetExtension('Notebook', assistantButton);
        app.commands.addCommand(assistantCellCommand, {
            icon: AssistantIcon,
            execute: getActivateAssistantWithCodeSelection({
                assistant: assistantSidebarWidget,
                notebookTracker
            })
        });
        app.commands.addKeyBinding({
            command: assistantToggleCommand,
            keys: ['Accel Shift A'],
            selector: 'Body'
        });
        app.commands.addKeyBinding({
            command: assistantFocusChatInputCommand,
            keys: ['Accel Shift C'],
            selector: 'Body'
        });
        const accountInterval = setInterval(async () => {
            const state = useStore.getState();
            if (state.account) {
                clearInterval(accountInterval);
                const context = app.shell.currentWidget?.context;
                const factoryName = context?.factoryName;
                const isNotebook = factoryName && factoryName.toLowerCase() === 'notebook';
                if (!isNotebook &&
                    !Boolean(window.localStorage.getItem('anaconda-assistant-opened-once'))) {
                    await app.commands.execute('notebook:create-new');
                    window.localStorage.setItem('anaconda-assistant-opened-once', 'true');
                }
                await state.updateAccount();
            }
        }, 100);
        // Update account information every 5 minutes,
        // in case the user changes their account tier.
        setInterval(async () => {
            const state = useStore.getState();
            await state.updateAccount();
        }, 1000 * 60 * 5);
        // Detecting logouts from other tabs
        window.addEventListener('storage', async (e) => {
            try {
                if (e.key === 'anaconda-assistant' && e.oldValue && e.newValue) {
                    const previousStorage = JSON.parse(e.oldValue);
                    const newStorage = JSON.parse(e.newValue);
                    const previousStorageState = previousStorage.state;
                    const newStorageState = newStorage.state;
                    const previousTermsVersion = previousStorage.terms.version;
                    const newTermsVersion = newStorage.terms.version;
                    if (!previousStorageState || !newStorageState) {
                        return;
                    }
                    if (previousTermsVersion !== null && newTermsVersion === null) {
                        await logout([
                            'An Assistant in another browser tab logged out.',
                            'You will need to log in again to use the Assistant.'
                        ].join(' '));
                    }
                }
            }
            catch (e) {
                console.error('Error detecting logouts from other tabs', e);
            }
        });
    }
}
